import React, { useEffect } from "react";
import { Link, graphql } from "gatsby";
import { getImage, GatsbyImage } from "gatsby-plugin-image";

import NavBar from "../components/navbar";
import MobileNav from "../mobileComponents/mobileNav";
import MobileFooter from "../mobileComponents/mobileFooter";
import "../components/servicesComponents/servicesPage.css";
import "../mobileComponents/mobileFooter.css";

const ServicesPage = ({ data }) => {

    useEffect(() => {
        const element = document.getElementById("servicesBullet");
        element.classList.add("filled");
    })

    const image = getImage(data.codeImage);
    
    return (
        <>
            <section className="servicesCont">
                <section className="servicesLeft">
                    <GatsbyImage image={image} alt="Coding Image" className="servicesImage" />
                </section>
                <section className="servicesRight">
                    <p className="servicesP1">I am currently looking for sites to build my Portfolio.</p>
                    <p className="servicesP2">I am offering my services free of charge.</p>
                    <p className="servicesP3">If you are interesting having a site built or maintained...</p>
                    <Link to="/contact"><div className="servicesContactButton"><p className="servicesContactButtonText">Contact Me Here!</p></div></Link>
                </section>
                <NavBar />
            </section>
            <MobileNav backgroundImage={data.siteLogoDark} currentPage="Services" />
            <section className="servicesMobileCont">
                <section className="servicesMobileImageAndText">
                    <GatsbyImage image={image} alt="Coding Image" className="servicesMobileImage" />
                    <section className="servicesMobileText">
                        <p className="servicesMobileP1">I am currently looking for opportunities to build my Portfolio.</p>
                        <p className="servicesMobileP2">I am offering my services free of charge.</p>
                        <p className="servicesMobileP3">If you are interested in having a site built or maintained...</p>
                        <Link to="/contact"><div className="servicesMobileContactButton"><p className="servicesMobileContactButtonText">Contact Me Here!</p></div></Link>
                    </section>
                </section>
            </section>
            <MobileFooter currentPage="Services" backToTop={false} />
        </>
    );
}

export const query = graphql`
    query MyServicesQuery {
        codeImage: file(relativeDirectory: {eq: "servicesImages"} name: {eq: "CodingScreen"}) {
            childImageSharp {
                gatsbyImageData(formats: [AUTO, WEBP, AVIF] placeholder: BLURRED width: 300)
            }
        }
        siteLogoDark: file(relativeDirectory: {eq: "homeImages"}, name: {eq: "SitesByJackLogo Dark No Background Smaller"})
        {
            childImageSharp {
                gatsbyImageData(width: 500 placeholder: BLURRED formats: [AUTO, WEBP, AVIF])
            }
        }
    }
`

export default ServicesPage